<template>
  <div class="farm flex flex-column page container-full">
    <header>
      <h1 class="text">{{ language.menu_3 }}</h1>
      <p>{{ language.tip_4_1 }}<br>{{ language.tip_4_2 }}</p>
      <div class="filter-bar flex jc-end ai-center">
        <m-switch v-if="false" v-model="filterFlag"><span class="text">{{language.tip_4_4}}</span></m-switch>
        <div class="flex">
<!--          <div class="sort-box flex jc-between ai-center text">-->
<!--            <span>{{ language.text_3_2 }}</span>-->
<!--            <img class="icon-s" src="../images/option/order1.png" alt="">-->
<!--          </div>-->
          <label class="search-box">
            <input type="text" v-model="searchText" :placeholder="language.tip_4_3">
          </label>
        </div>
      </div>
    </header>
    <div class="farm-container flex1 flex flex-wrap ai-start">
      <template v-for="pid in poolLength" :key="'pool-'+pid">
        <farm-round class="farm-item" :pid="pid-1" :filter-string="searchText"/>
      </template>
      <div v-if="loading" class="mask flex jc-center ai-center">
        <i class="icon-loading"></i>
      </div>
    </div>
  </div>
</template>

<script>
import {mapGetters} from "vuex";
import MSwitch from "@/components/mSwitch";
import FarmRound from "@/views/farm/farmRound";

export default {
  name: "farm",
  components: {FarmRound, MSwitch},
  computed:{
    ...mapGetters(['language']),
  },
  data(){
    return {
      filterFlag:false,
      searchText:'',
      poolLength:0,
      loading:false,
    }
  },
  methods:{
    getPoolList(){
      this.loading = true;
      this.$api.getPoolLength().then(length=>{
        this.poolLength = Number(length);
        setTimeout(()=>this.loading = false,500);
      }).catch(()=>this.loading = false);
    },
  },
  beforeMount() {
    this.getPoolList();
  }
}
</script>

<style lang="less" scoped>
header{
  height: 190px;
  margin: 20px 0 0;
  padding: 20px 30px 10px;
  background-color: #191f2d;
  border-radius: 20px;
  box-sizing: border-box;
  h1{
    height: 38px;
    margin: 0 0 15px;
  }
  p{
    height: 65px;
  }
  .sort-box{
    width: 130px;
    height: 40px;
    padding: 0 10px 0 20px;
    border-radius: 20px;
    background-color: #353B4D;
    cursor: default;
    img{
      transform: rotate(180deg);
    }
  }
  .search-box{
    background-color: #353B4D;
    margin-left: 25px;
    padding: 0 20px;
    border-radius: 20px;
    input{
      width: 120px;
      height: 40px;
    }
  }
}
.farm-container{
  margin: 10px -1.66666% 0;
  overflow-y: auto;
  position: relative;
  .mask{
    left: 1.66666%;
    right: 1.66666%;
    top: 10px;
    border-radius: 20px;
  }
}
.farm-item{
  width: 30%;
  min-width: 360px;
  margin: 10px 1.66666%;
}
::-webkit-scrollbar {
  display: none;
}
@media screen and (max-width: 1024px) {
  .farm{
    padding: 0 20px;
  }
  .farm-container{
    margin: 15px -10px 0;
    .mask{
      left: 10px;
      right: 10px;
      top: 0;
      border-radius: 20px;
    }
  }
  .farm-item{
    width: calc(50% - 20px);
    margin: 0 10px 10px;
  }
}
@media screen and (max-width: 768px) {
  .farm{
    padding: 0 15px;
  }
  .farm-container{
    margin: 15px -7.5px 0;
    .mask{
      left: 7.5px;
      right: 7.5px;
    }
  }
  .farm-item{
    width: calc(50% - 15px);
    margin: 0 7.5px 10px;
  }
}
@media screen and (max-width: 540px) {
  .farm{
    padding: 0 15px;
    height: auto;
  }
  header{
    margin: 10px 0;
    padding: 10px 15px;
    height: auto;
    h1{
      height: auto;
      font-size: 18px;
    }
    p{
      height: auto;
      font-size: 12px;
      margin-bottom: 15px;
    }
  }
  .farm-container{
    min-height: 400px;
    margin: 0 -1.66666%;
    overflow-y: visible;
  }
  .farm-item{
    width: 100%;
    min-width: 280px;
    margin: 0 1.66666% 10px;
  }
}
</style>